import { css, CSSResultArray, cssSelector, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const padding = { top: 11, bottom: 11, left: 11, right: 11 };
export const inset = 5;

export const scrollbarStyles: GenerateStyles = (tagName) => {
    const mainStyle = () => {
        const { root, is } = cssSelector(tagName);

        return css`
            :host {
                position: relative;
                display: grid;
                grid-template: 1fr / 1fr;
                height: 100%;
            }

            :host([border]) {
                border: 1px solid var(--color-secondary-3);
            }

            :host([outset]) {
                padding-right: 16px;
                padding-bottom: 16px;
            }
            :host([vert-padding]) {
                padding-top: var(--container-inner-spacing-top-bottom, 16px);
                padding-bottom: var(--container-inner-spacing-top-bottom, 16px);
                height: calc(100% - 2 * var(--container-inner-spacing-top-bottom, 16px));
            }
            :host([hori-padding]) {
                padding-left: var(--container-inner-spacing-left-right, 24px);
                padding-right: var(--container-inner-spacing-left-right, 24px);
            }

            ${root}::-webkit-scrollbar {
                display: none;
            }

            :host([dynamic-outset]:not([hori-hide])) ${root} {
                padding-bottom: 16px;
            }
            :host([dynamic-outset]:not([vert-hide])) ${root} {
                padding-right: 16px;
            }
            :host([dynamic-outset]) ${is('scroll-y-hidden')} {
                padding-right: 0 !important;
            }
            :host([dynamic-outset]) ${is('scroll-x-hidden')} {
                padding-bottom: 0 !important;
            }

            ${root} {
                height: 100%;
                overflow-x: auto;
                overflow-y: auto;
                scroll-behavior: auto;
                overscroll-behavior: auto;
                scroll-snap-type: none;

                scrollbar-width: none;
                -ms-overflow-style: none;
                scroll-padding: var(--scroll-padding, 0);
            }

            ${is('vertical-hide')} {
                overflow-y: hidden;
            }

            ${is('horizontal-hide')} {
                overflow-x: hidden;
            }
        `;
    };

    const yStyle = () => {
        const { root, child, is } = cssSelector(tagName + '-scroller-y');

        return css`
            ${root} {
                position: absolute;
                z-index: 4;

                width: 6px;
                right: ${inset}px;
                top: 0;
                bottom: 0;

                background-color: rgba(0, 0, 0, 0);
                padding-top: ${padding.top}px;
                padding-bottom: ${padding.bottom}px;
                background-clip: content-box;
            }

            :host([vertical-hide]) ${root}, ${is('hidden-y')} {
                display: none;
            }

            :host(:not([always-show])) ${root} {
                opacity: 0;
                transition: opacity 0.25s;
            }

            :host([drag-y]) ${root}, :host([hover]) ${root} {
                opacity: 1;
                transition: opacity 0s;
            }

            ${child('thumb')} {
                width: 8px;
                height: 20px;
                margin-left: 0;
                position: absolute;
                border-radius: 0;
                opacity: 1;
                left: -1px;
                top: 0;
                background-color: transparent;
                transition: background-color 0.25s;
            }

            ${child('inner')} {
                width: 6px;
                height: 100%;
                margin-left: 1px;
                background-color: #00000033;
                transition: background-color 0.25s;
            }

            ${child('thumb')}:hover,
            :host([drag-y]) ${child('thumb')} {
                background-color: #00000066;
            }

            ${child('thumb')}:hover ${child('inner')},
            :host([drag-y]) ${child('thumb')} ${child('inner')} {
                background-color: transparent;
            }
        `;
    };

    const xStyle = () => {
        const { root, child, is } = cssSelector(tagName + '-scroller-x');

        return css`
            ${root} {
                position: absolute;
                z-index: 4;

                height: 6px;
                right: 0;
                bottom: ${inset}px;
                left: 0;

                background-color: rgba(0, 0, 0, 0);
                padding-left: ${padding.left}px;
                padding-right: ${padding.right}px;
                background-clip: content-box;
            }

            :host([vertical-hide]) ${root}, ${is('hidden-x')} {
                display: none;
            }

            :host(:not([always-show])) ${root} {
                opacity: 0;
                transition: opacity 0.25s;
            }

            :host([drag-x]) ${root}, :host([hover]) ${root} {
                opacity: 1;
                transition: opacity 0s;
            }

            ${child('thumb')} {
                width: 20px;
                height: 8px;
                margin-left: 0;
                position: absolute;
                border-radius: 0;
                opacity: 1;
                left: 0;
                top: -1px;
                background-color: transparent;
                transition: background-color 0.25s;
            }

            ${child('inner')} {
                height: 6px;
                width: 100%;
                margin-top: 1px;
                background-color: #00000033;
                transition: background-color 0.25s;
            }

            ${child('thumb')}:hover,
            :host([drag-x]) ${child('thumb')} {
                background-color: #00000066;
            }

            ${child('thumb')}:hover ${child('inner')},
            :host([drag-x]) ${child('thumb')} ${child('inner')} {
                background-color: transparent;
            }
        `;
    };

    return [style, mainStyle(), yStyle(), xStyle()] as CSSResultArray;
};
