import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { padding, scrollbarStyles } from './scrollbarStyle';
import { ScrollbarProperties } from './scrollbarProperties';
import { version } from './metadata';
import {
    EventDispatcherMixin,
    ResizableBehaviorMixin,
    resizableBehaviorResizeEvent,
} from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'scrollbar', styles: scrollbarStyles, version })
export class Scrollbar
    extends ResizableBehaviorMixin(EventDispatcherMixin(BaseElement))
    implements ScrollbarProperties
{
    // Static members
    // suggested minimum scrollbar size is now 48px
    private static MIN_SCROLL_THUMB_HEIGHT = 48 + padding.top + padding.bottom;
    private static MIN_SCROLL_THUMB_WIDTH = 48 + padding.left + padding.right;

    // Public members
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    public border = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'vert-hide', reflect: true })
    public vertHide = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'hori-hide', reflect: true })
    public horiHide = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'always-show', reflect: true })
    public alwaysShow = false;

    @property({ type: Boolean, converter: booleanConverter, attribute: 'outset', reflect: true })
    public outset = false;
    @property({ type: Boolean, attribute: 'dynamic-outset', converter: booleanConverter, reflect: true })
    public dynamicOutset = false;
    @property({ type: Boolean, attribute: 'vert-padding', converter: booleanConverter, reflect: true })
    public vertPadding = false;
    @property({ type: Boolean, attribute: 'hori-padding', converter: booleanConverter, reflect: true })
    public horiPadding = false;

    // Specials for simulating scrollable html-element. Using the property decorator
    // we can override props and values on the html-element, which is else not possible.
    @property({ type: Number, attribute: false })
    get scrollTop() {
        return this._scrollTop;
    }

    set scrollTop(value) {
        if (value > -1 && this._scrollTop !== value) {
            this._scrollTop = value;
            if (this._scrollableElement) {
                this._scrollableElement.scrollTop = value;
            }
        }
    }

    @property({ type: Number, attribute: false })
    get scrollLeft() {
        return this._scrollLeft;
    }

    set scrollLeft(value) {
        if (value > -1 && this._scrollLeft !== value) {
            this._scrollLeft = value;
            if (this._scrollableElement) {
                this._scrollableElement.scrollLeft = value;
            }
        }
    }

    @query('div')
    private _scrollableElement;
    @query('div[class$="-scroller-y-thumb"]')
    private _scrollThumbElementY;
    @query('div[class$="-scroller-x-thumb"]')
    private _scrollThumbElementX;

    @state()
    private _scrollThumbHeight = Scrollbar.MIN_SCROLL_THUMB_HEIGHT;
    @state()
    private _scrollThumbWidth = Scrollbar.MIN_SCROLL_THUMB_WIDTH;
    @state()
    private _scrollThumbTop = 0;
    @state()
    private _scrollThumbLeft = 0;
    @state()
    private _scrollThumbDraggingY = false;
    @state()
    private _scrollThumbDraggingX = false;
    @state()
    private _scrollVisibleY = false;
    @state()
    private _scrollVisibleX = false;

    private _animationFrame: number | null = null;
    private _lastScrollThumbPositionY = 0;
    private _lastScrollThumbPositionX = 0;

    private _resizeObserver;
    private _resizeObserverTargets: Node[] = [];

    private _scrollTop = 0;
    private _scrollLeft = 0;

    // Protected members

    constructor() {
        super();

        this._onMouseOver = this._onMouseOver.bind(this);
        this._onMouseOut = this._onMouseOut.bind(this);
        this._onScroll = this._onScroll.bind(this);
        this._handleSlotchange = this._handleSlotchange.bind(this);
        this._handleResizeEvent = this._handleResizeEvent.bind(this);
        this._onThumbYMouseDown = this._onThumbYMouseDown.bind(this);
        this._onThumbXMouseDown = this._onThumbXMouseDown.bind(this);
        this._onThumbMouseUp = this._onThumbMouseUp.bind(this);
        this._onThumbMouseDrag = this._onThumbMouseDrag.bind(this);
    }

    connectedCallback() {
        super.connectedCallback();

        this.addEventListener('mouseenter', this._onMouseOver);
        this.addEventListener('mouseleave', this._onMouseOut);
        this.addEventListener('scroll-left-home', this._onScrollLeftHome);

        this.addEventListener(resizableBehaviorResizeEvent, this._handleResizeEvent);
    }

    disconnectedCallback() {
        this.removeEventListener('mouseenter', this._onMouseOver);
        this.removeEventListener('mouseleave', this._onMouseOut);
        this.removeEventListener('scroll-left-home', this._onScrollLeftHome);
        this.removeEventListener(resizableBehaviorResizeEvent, this._handleResizeEvent);
        super.disconnectedCallback();
    }

    public scrollTo(options) {
        // jsdom protection...
        if (this._scrollableElement && this._scrollableElement.scrollTo) {
            this._scrollableElement.scrollTo(options);
        }
    }

    public scrollBy(options) {
        if (this._scrollableElement && this._scrollableElement.scrollBy) {
            this._scrollableElement.scrollBy(options);
        }
    }

    get scrollWidth(): number {
        return this._scrollableElement.scrollWidth;
    }

    get scrollHeight(): number {
        return this._scrollableElement.scrollHeight;
    }

    public getScrollHeight() {
        return this.scrollHeight;
    }

    public getScrollTop() {
        return this._scrollTop;
    }

    public getScrollLeft() {
        return this._scrollLeft;
    }

    public scrollLeftHome() {
        this._scrollableElement.scrollLeft = 0;
    }

    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);
        this._scrollableElement.addEventListener('scroll', this._onScroll);
        this._scrollThumbElementY.addEventListener('mousedown', this._onThumbYMouseDown);
        this._scrollThumbElementX.addEventListener('mousedown', this._onThumbXMouseDown);
        this._installResizeObserver();
    }

    protected updated(_changedProperties) {
        super.updated(_changedProperties);

        for (const [prop, cssVar] of Object.entries({
            _scrollVisibleX: '--scrollbar-x-overflow',
            _scrollVisibleY: '--scrollbar-y-overflow',
        })) {
            if (_changedProperties.has(prop)) {
                if ((this as any)[prop]) {
                    this._scrollableElement.style.setProperty(cssVar, '1');
                } else {
                    this._scrollableElement.style.setProperty(cssVar, '0');
                }
            }
        }
    }

    //
    // Private members
    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            ['vertical-hide']: this.vertHide,
            ['horizontal-hide']: this.horiHide,
            ['scroll-x-hidden']: !this._scrollVisibleX,
            ['scroll-y-hidden']: !this._scrollVisibleY,
        });

        return html`
            <div class="${rootClasses}">
                <slot @slotchange="${this._handleSlotchange}"></slot>
            </div>
            ${this._renderScrollY(baseClassName)} ${this._renderScrollX(baseClassName)}
        `;
    }

    private _renderScrollY(baseClassName) {
        const [baseClassNameY, rootClassesY] = createClassName(`${baseClassName}-scroller-y`, {
            ['hidden-y']: !this._scrollVisibleY || this.vertHide,
        });
        return html`
            <div class="${rootClassesY}">
                <div
                    class="${baseClassNameY}-thumb"
                    style="height: ${this._scrollThumbHeight - padding.top - padding.bottom}px; top: ${this
                        ._scrollThumbTop + padding.top}px;"
                >
                    <div class="${baseClassNameY}-inner"></div>
                </div>
            </div>
        `;
    }

    private _renderScrollX(baseClassName) {
        const [baseClassNameX, rootClassesX] = createClassName(`${baseClassName}-scroller-x`, {
            ['hidden-x']: !this._scrollVisibleX || this.horiHide,
        });
        return html`
            <div class="${rootClassesX}">
                <div
                    class="${baseClassNameX}-thumb"
                    style="width: ${this._scrollThumbWidth - padding.left - padding.right}px; left: ${this
                        ._scrollThumbLeft + padding.left}px;"
                >
                    <div class="${baseClassNameX}-inner"></div>
                </div>
            </div>
        `;
    }

    private _handleSlotchange(e) {
        const target = e.target as HTMLSlotElement;
        this._resizeObserverTargets.forEach((c) => {
            this._resizeObserver.unobserve(c);
        });
        this._resizeObserverTargets = [];
        target
            .assignedNodes()
            .filter((node) => node.nodeType === 1 && node)
            .forEach((c) => {
                this._resizeObserver.observe(c);
                this._resizeObserverTargets.push(c);
            });

        this._recalc();
    }

    private _handleResizeEvent() {
        this._recalc();
    }

    private _onThumbYMouseDown(e) {
        e.preventDefault();
        e.stopPropagation();

        this._lastScrollThumbPositionY = e.clientY;
        this._scrollThumbDraggingY = true;
        this.setAttribute('drag-y', '');
        document.addEventListener('mouseup', this._onThumbMouseUp);
        document.addEventListener('mousemove', this._onThumbMouseDrag);
        document.body.style.setProperty('pointer-events', 'none');
    }

    private _onThumbXMouseDown(e) {
        e.preventDefault();
        e.stopPropagation();

        this._lastScrollThumbPositionX = e.clientX;
        this._scrollThumbDraggingX = true;
        this.setAttribute('drag-x', '');
        document.addEventListener('mouseup', this._onThumbMouseUp);
        document.addEventListener('mousemove', this._onThumbMouseDrag);
        document.body.style.setProperty('pointer-events', 'none');
    }

    private _onThumbMouseUp(e) {
        if (this._scrollThumbDraggingY) {
            e.preventDefault();
            this._scrollThumbDraggingY = false;
            this.removeAttribute('drag-y');
        }
        if (this._scrollThumbDraggingX) {
            e.preventDefault();
            this._scrollThumbDraggingX = false;
            this.removeAttribute('drag-x');
        }
        document.removeEventListener('mouseup', this._onThumbMouseUp);
        document.removeEventListener('mousemove', this._onThumbMouseDrag);
        document.body.style.removeProperty('pointer-events');
    }

    private _onThumbMouseDrag(e) {
        if (this._scrollThumbDraggingY || this._scrollThumbDraggingX) {
            e.preventDefault();
            e.stopPropagation();
            const scrollHostElement = this._scrollableElement;
            const { offsetHeight, offsetWidth, scrollHeight, scrollWidth } = scrollHostElement;
            const thisHeight = this.offsetHeight;
            const thisWidth = this.offsetWidth;

            if (this._scrollThumbDraggingY) {
                const deltaY = e.clientY - this._lastScrollThumbPositionY;
                const newScrollHostTop = (scrollHeight / offsetHeight) * deltaY;
                this._lastScrollThumbPositionY = e.clientY;
                const coeff = thisHeight / offsetHeight;
                const newTop = Math.min(
                    Math.max(0, this._scrollThumbTop + deltaY * coeff),
                    thisHeight - this._scrollThumbHeight
                );

                this._scrollThumbTop = newTop;
                scrollHostElement.scrollTop = Math.min(
                    scrollHostElement.scrollTop + newScrollHostTop,
                    scrollHeight - offsetHeight
                );
                this._scrollTop = scrollHostElement.scrollTop;
            }
            if (this._scrollThumbDraggingX) {
                const deltaX = e.clientX - this._lastScrollThumbPositionX;
                const newScrollHostLeft = (scrollWidth / offsetWidth) * deltaX;
                this._lastScrollThumbPositionX = e.clientX;
                const coeff = thisWidth / offsetWidth;
                this._scrollThumbLeft = Math.min(
                    Math.max(0, this._scrollThumbLeft + deltaX * coeff),
                    thisWidth - this._scrollThumbWidth
                );
                scrollHostElement.scrollLeft = Math.min(
                    scrollHostElement.scrollLeft + newScrollHostLeft,
                    scrollWidth - offsetWidth
                );
                this._scrollLeft = scrollHostElement.scrollLeft;
            }
        }
    }

    private async _onScroll() {
        if (this._scrollableElement) {
            const scrollHostElement = this._scrollableElement;

            const { scrollTop, scrollHeight, scrollLeft, scrollWidth, clientHeight, clientWidth } = scrollHostElement;
            const { offsetHeight, offsetWidth, _scrollThumbHeight: thumbHeight, _scrollThumbWidth: thumbWidth } = this;

            const newTop = (scrollTop / (scrollHeight - clientHeight)) * (offsetHeight - thumbHeight);
            const newLeft = (scrollLeft / (scrollWidth - clientWidth)) * (offsetWidth - thumbWidth);

            this._scrollThumbTop = Math.min(newTop, offsetHeight - thumbHeight);
            this._scrollThumbLeft = Math.min(newLeft, offsetWidth - thumbWidth);

            this._scrollTop = scrollHostElement.scrollTop;
            this._scrollLeft = scrollHostElement.scrollLeft;
        }
        await this.updateComplete;
        window.dispatchEvent(new Event('scroll'));
        this.dispatch('scroll');
    }

    private _onMouseOver() {
        /*
        Force recalc on mouse over. This should not be needed but seems to be the
        only way to get the scrollbars size correct. For example when dynamic content
        changes and/or on first load.
        Will degrade performance though!
        */
        this._recalc();

        this.setAttribute('hover', '');
    }

    private _onMouseOut() {
        this.removeAttribute('hover');
    }

    private _installResizeObserver() {
        this._resizeObserver = new ResizeObserver(() => {
            if (this._animationFrame === null) {
                this._animationFrame = requestAnimationFrame(() => {
                    this._animationFrame = null;
                    this._recalc();
                });
            }
        });
        this._resizeObserver.observe(this._scrollableElement);
    }

    private _recalc() {
        const { scrollHeight, scrollWidth, clientHeight, clientWidth } = this._scrollableElement;
        const thisHeight = this.clientHeight;
        const thisWidth = this.clientWidth;
        const coeffHeight = thisHeight / clientHeight;
        const coeffWidth = thisWidth / clientWidth;

        const scrollThumbPercentageY = clientHeight / scrollHeight;
        this._scrollThumbHeight = Math.max(
            scrollThumbPercentageY * clientHeight * coeffHeight,
            Scrollbar.MIN_SCROLL_THUMB_HEIGHT
        );

        const scrollThumbPercentageX = clientWidth / scrollWidth;
        this._scrollThumbWidth = Math.max(
            scrollThumbPercentageX * clientWidth * coeffWidth,
            Scrollbar.MIN_SCROLL_THUMB_WIDTH
        );

        this._scrollVisibleY = scrollHeight > clientHeight;
        this._scrollVisibleX = scrollWidth > clientWidth;
    }

    /**
     * Handle scroll-left-home from any child
     *
     * @param e
     * @private
     */
    private _onScrollLeftHome(e) {
        e.stopPropagation();
        this.scrollLeftHome();
    }
}
